import React from 'react';



export default function Notification(currentUser, nearConfig, message) {
  const { networkId } = nearConfig
  const urlPrefix = `https://explorer.${networkId}.near.org/accounts`
  return (
    <aside>
      <a target="_blank" rel="noreferrer" href={`${urlPrefix}/${currentUser.accountId}`}>
        {currentUser.accountId}
      </a>
      {' '/* React trims whitespace around tags; insert literal space character when needed */}
      test{message}
      <footer>
        <div> Succeeded</div>
        <div>Just now</div>
      </footer>
    </aside>
  )
}
